.a {
  height: 100vh;
  display: flex;
  align-items: center;
}

.a-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}

.a-right {
  flex: 1;
}

.a-card.bg {
  position: absolute;
  top: 50px;
  left: 50px;
  background-color: #003566;
}

.a-card {
  width: 60%;
  height: 70vh;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
}

.a-card:last-child {
  background-image: url("LazAndCathyBeach.jpg");
  background-position: center;
  background-size: cover;
}

.a-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 30px;
}

.a-award-img {
  width: 120px;
  height: 120px;
}

.a-title {
  font-weight: 400;
}

.a-sub {
  margin: 20px 0;
}

.a-desc{
    padding-right: 10px;
    font-size: 150%;
    font-weight: 300;
    text-indent: 5%;
}

.a-award{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.a-award-texts{
    width: 70%;
}

.a-award-title{
    font-weight: bold;
    color: #555;
    margin-bottom: 10px;
}

.fig {
  color: #003566;
  font-weight: 600;
}

.quote {
  color: #003566;
  font-weight: 600;
}

@media screen and (max-width:480px) {
    .a {
      flex-direction: column;
      text-align: center;
      margin-top: 50px;
    }

    .a-left {
      width: 100%;
    }

    .a-card {
      height: 30vh;
    }
    .a-card:last-child {
      background-image: url("secondMe.jpg");
      background-position: top;
      background-size: cover;
    }

    .a-card.bg, .a-award {
      display: none;
    }

    .a-right{
      padding: 20px;
    }
  }